.contacts {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: $bg-color;
}

.contacts .wrapper {
	display: flex;
	flex-direction: column;
}

.contacts__container {
	margin-top: 40px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.contacnt__phone {
	font-size: 32px;
	color: $elem-color;
	text-decoration: underline;
}

.contacts__chat-icons,
.contacts__social {
	margin-top: 20px;
	margin-bottom: 20px;
	display: flex;
	justify-content: space-around;
	width: 200px;
}

.contacts__chat-icon {
	width: 40px;
	height: 40px;

	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	opacity: 0.6;

	&:hover {
		opacity: 1;
	}
}

.contacts__chat-icon--whatsup {
	background-image: url('../img/whatsup-icon.svg');
}

.contacts__chat-icon--viber {
	background-image: url('../img/viber-icon.svg');
}

.contacts__chat-icon--telegram {
	background-image: url('../img/telegram-icon.svg');
}

.contacts__social-icon {
	width: 60px;
	height: 60px;

	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	opacity: 0.6;

	&:hover {
		opacity: 1;
	}
}

.contacts__social-icon--vk {
	background-image: url('../img/vk-icon.svg');
}

.contacts__social-icon--instagram {
	background-image: url('../img/instagram-icon.svg');
}

.contact__map {
	margin-top: 20px;
	display: flex;
	flex-direction: column;
	width: 100%;

	& iframe {
		border: none;
		width: 100%;
		height: 350px;
	}
}