.page-footer {
	border-top: 1px solid #333333;
	padding-top: 40px;
	background-color: $bg-color;
}

.page-footer__logo img {
	width: 150px;
}

.page-footer__menu {
	margin-top: 20px;
	display: flex;
	flex-wrap: wrap;
}

.page-footer__item {
	margin-bottom: 15px;
	margin-right: 15px;
}

.page-footer__link {
	font-size: 16px;
	color: $text-color;
	opacity: 0.3;

	&:hover {
		opacity: 0.8;
	}
}

.page-footer__container	{
	display: flex;
	flex-direction: column;
	align-items: center;
}

.page-footer__copyright {
	margin-top: 40px;
	margin-bottom: 40px;
	text-align: center;
}

.page-footer__copyright p {
	color: $text-color;
	opacity: 0.3;
	font-size: 12px;
}

.page-footer__copyright a {
	font-size: 12px;
	color: $elem-color;
	

	&:hover {
		text-decoration: underline;
	}
}