.slider {
	position: relative;
	margin-top: 40px;
	max-height: 300px;
}

.slider__controls {
	display: flex;
	justify-content: space-between;
	width: 100%;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: 3;
}

.slider__button {
	position: relative;
	width: 35px;
	height: 35px;

	background-color: rgba(0, 0, 0, 0.3);
	border: 2px solid $elem-color;
	border-radius: 50%;
	opacity: 0.6;
	cursor: pointer;

	&::after {
		content: "";
		position: absolute;
		top: 8px;
		left: 8px;

		width: 10px;
		height: 10px;

		border: 5px solid $elem-color;
		border-bottom: none;
		border-right: none;
	}

	&:hover {
		opacity: 1;
	}
}

.slider__button--prev {
	&::after {
		transform: rotate(-45deg);
	}
}

.slider__button--next {
	&::after {
		transform: rotate(135deg);
	}
}

.slider__slides {
	overflow: hidden;
	height: 300px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.slider__item img {
	width: 100%;
}