.timer__description {
	font-size: $titles-fs-mobile;
	line-height: 20px;
}

.timer {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.timer__numbers {
	margin-top: 20px;
}

.timer__number {
	font-size: 42px;
	font-weight: bold;
	color: $elem-color;

	&:not(:last-child) {
		margin-right: 20px;
	}
}