.sale {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: $bg-color;
	color: $text-color;
}

.sale .wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.sale__description {
	margin-top: 20px;
	text-align: center;
	font-size: $description-fs-mobile;
	line-height: 28px;

	& .sale__text {
		color: $elem-color;
		font-size: 24px;
	}
}

.sale__old-price {
	margin-left: 10px;
	position: relative;
	color: #666666;

	&::after {
		content: "";
		position: absolute;
		top: 8px;
		left: 0;
		height: 3px;
		width: 50px;
		transform: rotate(-15deg);

		background-color: #666666;
	}
}

.sale__new-price {
	margin-left: 10px;
	font-size: 28px;
	color: $elem-color;
}

.sale__button {
	margin-top: 40px;
}