@keyframes slidedown {
	0% {
		transform: translateY(-100%);
	}

	100% {
		transform: translateY(0);
	}
}

.price {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: $bg-color;
}

.price__list {
	overflow: hidden;
	margin-top: 20px;
}

.price__item {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	color: $text-color;
	cursor: pointer;	
}

.price__btn {
	width: 100%;
	padding-top: 15px;
	padding-bottom: 15px;
	font-size: 24px;
	text-align: left;
	background-color: $bg-color;
	z-index: 2;

	&::after {
		content: "";
		position: absolute;

		top: 20px;
		right: 20px;

		height: 10px;
		width: 10px;
		background-color: transparent;
		border: 3px solid #ffffff;
		border-right: none;
		border-top: none;
		transform: rotate(-45deg);
	}
}

.price__submenu {
	position: relative;
	display: none;
	padding: 10px;
	width: 100%;
	background-color: $text-color;
}

.price__subitem {
	padding: 10px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: $bg-color;
	margin-bottom: 10px;
}

.price__name {
	max-width: 60%;
	font-size: 16px;
	font-weight: normal;
}

.price__price {
	font-size: 16px;
	font-weight: bold;
}

.price__subitem--active {
	animation: slidedown 0.4s ease-out;
	display: block;
}

.price__btn--active {
	color: $elem-color;

	&::after {
		top: 25px;
		transform: rotate(135deg);
		transition: 0.3s;
		border-color: $elem-color;

	}
}
