.features__title {
	font-size: 18px;
	color: $elem-color;
	z-index: 2;
	background-color: $bg-color;
	padding-right: 10px;
	padding-left: 10px;
}

.features__description {
	margin-top: 20px;
	font-size: 16px;
}

.features__item {
	margin-bottom: 40px;
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;

	&::after {
		position: absolute;
		content: "";
		top: 10px;
		left: 0;
		width: 100%;
		height: 1px;
		background-color: $elem-color;
		z-index: 1;
	}
}
