.form {
	margin-top: 40px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.form__item {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 20px;

	& label {
		font-size: 20px;
		text-transform: uppercase;
	}

	& input {
		padding: 10px;
		margin-top: 10px;
		border: 1px solid $elem-color;
		border-radius: 5px;
		color: $bg-color;
		font-size: 18px;

	}
}

.form__submit {
	margin-top: 40px;
}